/* Definición de variables para colores y tipografía */
:root {
  --text-color: #003b4a; /* Color del texto principal */
  --text-color-a: rgb(255, 255, 255); /* Color del texto alternativo */
  --contrast-color: rgb(191, 12, 240); /* Color de contraste */
  --contrast-color-a: rgb(229, 225, 254); /* Color de contraste alternativo */
  --body-color: rgb(0, 59, 74); /* Color de fondo del cuerpo */
  --font-alternate: 'Montserrat Alternates', sans-serif; /* Fuente alternativa */
  --footer-color: #4ba39380; /* Color de fondo del footer */
  --blockquote-color: rgba(255, 215, 0, 0.8); /* Color del bloque de cita */
}

/* Estilos generales */
.header {
  font-weight: 600;
  background-color: rgb(241, 245, 249);
  padding-bottom: 0.5rem;
  align-content: center;


}
.contenido {
  text-align: center;
  padding: 0%;
  
  }
  .contenido li {
    display: inline-block;
    vertical-align: top; 
  
  
  }
  /* Estilos para el menú desplegable */
.perota_menu {
  background-image: url('/src/img/perota.jpeg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  height: 60vh;
  box-sizing: border-box; 
}

/*margenes pagina portada*/

.general {
  margin-left: 0.5rem;
  padding: 0.3rem;
  box-sizing: border-box;
  margin-right: 0.5rem;
  text-decoration: none;
  margin-top: 0.5rem;


}

figure {
  font-size: 0.6rem;
  text-align: left;
  margin-left: auto;
}
figure a{
  color: var(--blockquote-color);
}

body {
  font-family: var(--font-alternate), sans-serif; /* Cambia la fuente a Montserrat Alternates */
  font-weight: 400;
  color: var(--contrast-color-a);
  background-color: var(--body-color);
  width: 100%;
  height: 100%;
  
}

h1 {
  font-weight: 900;
  font-size: smaller;
  font-family: var(--font-alternate), sans-serif; /* Cambia la fuente a Montserrat Alternates */
  width: 100%;
  text-align: center;

}



h2 {

  text-align: center;
  font-size:large;
}

h3 {
  font-size:medium;
}

/* Estilos para las imágenes de las canciones */
.portada img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.discos {
  text-align: left;

}
.discos li{
  list-style: none;
  text-align: justify;
  padding-top: 1rem;
 
}

.discos a {
  color: var(--blockquote-color);
}



.section1 a{
 
  color: var(--blockquote-color);
  
}


.boton {
  background-color: var(--text-color-a);
  cursor: pointer;
  border-radius: 155px;
  transition: background-color 0.3s;
  align-items: center;
  padding: 5px 5px;
}

.contenido a {
  color: var(--text-color);
  text-decoration: none;
}
.Mas_info p{

  text-align: end;
}
.Mas_info a{
  color: var(--text-color);
  text-decoration: none;
  
}
.album {
 
  padding: 1rem;
}

/* Estilos para el botón de "Me interesa" */
.info button {
  background-color: var(--text-color-a);
  cursor: pointer;
  border-radius: 20px;
  transition: background-color 0.2s;
  padding: 8px 8px;
 

}
/* Estilos para los enlaces en estado normal */
.tour a {
  color: inherit; 
  text-decoration: none;
  transition: color 0.3s; /* Transición de color más rápida */
}

.info button:hover,
.boton:hover {
  background-color: var(--contrast-color); /* Cambia el color de fondo al hacer hover */
  color: white; /* Cambia el color del texto al hacer hover */
}

.boton.activo {
  text-decoration: underline; /* Subrayado para el enlace activo */
}



/* Estilos para comentarios y categorías */
blockquote {
  width: 90%;
  margin: 1rem auto;
  padding-left: 0.5rem;
  border-left: 4px solid var(--blockquote-color);
  font-size: 0.9rem;
}
/*Estilos para tabala*/

table {
  border-collapse: collapse;
  width: 100%;
}
th, td {
  border: 1px solid var(--contrast-color-a);
  padding: 8px;
  text-align: left;
}
th {
  background-color:var(--footer-color);
  
}

.tour a:hover {
  color:var(--contrast-color);
}

/* Estilos para slide */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

.mySlides.fade { 
  display: none;
}
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}


.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}
/* Estilos para el pie de página */
footer {
  background-color: var(--footer-color);
  width: 100%;
  display: flex;
  flex-direction: column;
  
}


footer a {

  color: white;
  text-decoration: none;
  transition: color 0.2s;

}
.footer_width li{
 
 text-align: center;
  width: 30%;
}
.redes{
  margin: 0;

}
.redes ul {
  list-style: none; 
  display: flex;
  justify-content: center;

}


footer a:hover {
  color: #a4f479;
}

footer ul {
  display: flex;
  list-style: none;
  padding: 0;

}

footer ul li {
  margin: 0;
  
}
.footer_width{
  text-align: center;
  
}
.footer_info {
  border: 0.5mm solid red;
  line-height: normal;
  margin: 0;
  text-align: center;
}



/* Media queries para dispositivos móviles y tabletas */
@media screen and (min-width: 768px) {
 
  .perota_menu {
    height: 100vh; 
  }
}
@media screen and (max-width: 768px) {

  h1 {
    font-size: 1.2rem;
  }

  .album img {
    width: 100%; /* Ajustar la anchura de la imagen al 100% para dispositivos móviles */
  }

  .name {
    margin-left: 0; /* Restablecer el margen izquierdo para dispositivos móviles */
    font-size: 1.3rem; /* Reducir el tamaño de la fuente para dispositivos móviles */
  }

  .info button {
    margin-left: 0; /* Restablecer el margen izquierdo para dispositivos móviles */
  }

  footer {
    font-size: 0.8rem;
  }

  .video iframe {
    width: 100%; /* Ajustar el ancho del iframe al 100% */
    height: calc(100vw * 0.5625); /* Establecer la altura del iframe como automática */
  }
}

/* Media queries para computadoras de escritorio */
@media screen and (min-width: 769px) {
  .video {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; /* Proporción 16:9 para el contenedor del video */
    height: 0; 
    overflow: hidden;
  }

  .video iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
